// Intl.NumberFormat polyfill: https://formatjs.io/docs/polyfills/intl-numberformat/#via-polyfillio
// import '@formatjs/intl-getcanonicallocales/polyfill' // is needed by intl-locale
// import '@formatjs/intl-locale/polyfill-force' // is needed by intl-numberformat
// import '@formatjs/intl-numberformat/polyfill-force'
// import '@formatjs/intl-numberformat/locale-data/no'
// import '@formatjs/intl-numberformat/locale-data/en'

import {
  Section,
  NumberFormat,
  Space,
  Dl,
  Dt,
  Dd,
  Hr,
  H1,
  H2,
  H3,
} from '@dnb/eufemia'
import { Provider } from '@dnb/eufemia/shared'
import styled from '@emotion/styled'

const List = styled(Dl)`
  display: flex;
  flex-direction: row;

  dt,
  dd {
    display: block;
    margin: 0 3rem 0 0;
  }

  dd {
    min-width: 120px;
  }
`

type BlockProps = {
  children: number
}

const Block = ({ children }: BlockProps) => {
  return (
    <Space bottom top>
      <H3 bottom>{children}</H3>
      <List>
        <Dt>0</Dt>
        <Dd>
          <NumberFormat compact value={children} />
        </Dd>
        <Dd>
          {new Intl.NumberFormat('nb-NO', {
            // @ts-ignore
            notation: 'compact',
          }).format(children)}
        </Dd>
      </List>

      <List>
        <Dt>1</Dt>
        <Dd>
          <NumberFormat compact decimals={1} value={children} />
        </Dd>
        <Dd>
          {new Intl.NumberFormat('nb-NO', {
            // @ts-ignore
            notation: 'compact',
            maximumSignificantDigits: 1,
          }).format(children)}
        </Dd>
      </List>

      <List>
        <Dt>2</Dt>
        <Dd>
          <NumberFormat compact decimals={2} value={children} />
        </Dd>
        <Dd>
          {new Intl.NumberFormat('nb-NO', {
            // @ts-ignore
            notation: 'compact',
            maximumSignificantDigits: 2,
          }).format(children)}
        </Dd>
      </List>

      <List>
        <Dt>3</Dt>
        <Dd>
          <NumberFormat compact decimals={3} value={children} />
        </Dd>
        <Dd>
          {new Intl.NumberFormat('nb-NO', {
            // @ts-ignore
            notation: 'compact',
            maximumSignificantDigits: 3,
          }).format(children)}
        </Dd>
      </List>

      <List>
        <Dt>4</Dt>
        <Dd>
          <NumberFormat compact decimals={4} value={children} />
        </Dd>
        <Dd>
          {new Intl.NumberFormat('nb-NO', {
            // @ts-ignore
            notation: 'compact',
            maximumSignificantDigits: 4,
          }).format(children)}
        </Dd>
      </List>
    </Space>
  )
}

const Blocks = () => {
  return (
    <>
      <Block>{1234}</Block>
      <Hr />
      <Block>{12345}</Block>
      <Hr />
      <Block>{123456}</Block>
      <Hr />
      <Block>{1234567}</Block>
      <Hr />
      <Block>{12345678}</Block>
      <Hr />
      <Block>{123456789}</Block>
      <Hr />
      <Block>{1234567891}</Block>
      <Hr />
      <Block>{12345678912}</Block>
      <Hr />
      <Block>{123456789123}</Block>
    </>
  )
}

export default function App() {
  return (
    <Section spacing left>
      <H1 size="medium" bottom={2}>
        Comparison of shorten numbers with up to 4 decimals
      </H1>

      <Provider>
        <Space>
          <H2 top="large">nb-NO</H2>
          <Blocks />
        </Space>
      </Provider>

      <Provider locale="en-GB">
        <Space>
          <H2 top="large">en-GB</H2>
          <Blocks />
        </Space>
      </Provider>
    </Section>
  )
}
